import { DocumentsCollectionInfoEntity } from "@rp/react/tanstack-api/documents"

const getEventEntityInfo = (
  entities: Array<DocumentsCollectionInfoEntity>,
): { name: string; ticker: string | undefined; logoUrl: string | undefined } => {
  const firstWithTickerAndIcon = entities.find(entity => entity.ticker && entity.logoUrl)

  if (firstWithTickerAndIcon) {
    return {
      name: firstWithTickerAndIcon.name,
      ticker: firstWithTickerAndIcon.ticker,
      logoUrl: firstWithTickerAndIcon.logoUrl,
    }
  }

  const firstWithTickerOrIcon = entities.find(entity => entity.ticker || entity.logoUrl)

  if (firstWithTickerOrIcon) {
    return {
      name: firstWithTickerOrIcon.name,
      ticker: firstWithTickerOrIcon.ticker,
      logoUrl: firstWithTickerOrIcon.logoUrl,
    }
  }

  return {
    name: entities[0]?.name || "N/A",
    ticker: undefined,
    logoUrl: undefined,
  }
}

export { getEventEntityInfo }
